//
// --------------------------------------------------
// 模块：前台页面
// --------------------------------------------------
// bootstrap 默认Less
@import "../bootstrap/variables.less";
@import "../bootstrap/mixins/gradients.less";
@import "../bootstrap/utilities.less";
@import "../bootstrap/mixins/clearfix.less";
@import "../bootstrap/mixins/center-block.less";
@import "../bootstrap/mixins/hide-text.less";
// 思诺博UI Less
@import "../sinoui-variables.less";    // 变量

.bg-body {
    padding: 8px;
    background-color: #f8f8f8;

    .body-side {
        width: 250px;
    }

    .body-main {
        width: 927px;
    }
    ul {
        padding: 0;
        &:extend(.clearfix all);
    }
    a {
        color:  @gray;
    }
}
.bg-white {
    background-color: @white;
}
.bd-gray {
    border: 1px solid #f0f0f0;
}
.ico-common {
    background: url(images/icon-common.png) no-repeat;
    opacity: .8;
    &:hover {
        opacity: 1;
    }
}
.ico-notice {
    background-position: 0 0;
}
.ico-more {
    background-position: 0 -26px;
}
.ico-config {
    background-position: -18px -26px;
}
.ico-refresh {
    background-position: -36px -26px;
}
.ico-fun {
    background-position: -54px -26px;
}
.ico-chart {
    background-position: -72px -26px;
}
.ico-new {
    background-position: -26px 0;
}
.ico-history {
    background-position: -52px 0;
}
.ico-hot {
    background-position: -78px 0;
}
.ico-report {
    background-position: -104px 0;
}
.ico-use {
    background-position: -130px 0;
}
.ico-product {
    background-position: -156px 0;
}
.ico-company {
    background-position: -182px 0;
}
.ico-cart {
    background-position: -208px 0;
}
.ico-chartline {
    background-position: -234px 0;
}
.ico-work {
    background-position: -260px 0;
}
.ico-network {
    background-position: -286px 0;
}

.ico-work-order {
    background-position: 0 -64px;
}
.ico-work-account {
    background-position: -30px -64px;
}
.ico-work-material {
    background-position: -60px -64px;
}
.ico-work-supplier {
    background-position: -90px -64px;
}
.ico-work-add {
    background-position: -120px -64px;
}

.notice {
    .notcie-title {
        width: 6.5%;
        font-weight: 700;
    }
    .swiper-container {
        width: 93.5%;
        height: 100%;
    }
}


.box-title {
    line-height: 46px;
    &:extend(.clearfix all);
    span {
        margin: 10px;
        line-height: 26px;
        display: inline-block;
        float: left;
        font-size: @font-size-16;
        &.ico-common {
            width: 26px;
            height: 26px;
            margin-right: 0;
        }
        a {
            margin-right: 5px;
            margin-top: 3px;
            width: 18px;
            height: 18px;
            display: inline-block;
        }
    }
    .btn-group {
        .dropdown-menu {
            min-width: 60px;
            left: auto;
            padding: 2px 0;
            margin-top: -5px;
            border-color: #9fc5f4;
            a {
                width: 100%;
                height: auto;
                padding: 3px 10px;
            }
        }
    }
}
.box-news {
    height: 184px;
    padding: 0 10px;
    li {
        padding: 6px 0;
        span {
            float: right;
        }
    }
}

.box-history {
    height: 308px;
    .thumbnail {
        padding: 17px 8px;
        border: 0;
        border-radius: 0;
        margin-bottom: 0;
        border-top: 1px solid #f0f0f0;
        &:first-child {
            border-top: 0;
            padding-top: 10px;
        }
        img {
            float: left;
            margin-right: 10px;
        }
        .caption {
            padding-bottom: 0;
            p {
                color: @gray-light;
            }
        }
        h3 {
            margin-top: 0;
        }
    }
}

.box-goods {
    height: 246px;
    padding: 5px;
    overflow: hidden;
    &.tab-content {
        > .tab-pane {
            height: 232px;
            position: relative;
            display: block;
            visibility: hidden;
        }
        > .active {
            visibility: inherit;
        }
    }
    .thumbnail {
        margin-bottom: 0;
        border-color: transparent;
        border-radius: 0;
        cursor: pointer;
        &:hover {
            border-color: #f7c1b7;
        }
        .caption {
            padding: 5px;
        }
        h3 {
            margin-top: 5px;
            margin-bottom: 8px;
        }
        p {
            margin-bottom: 0;
            label {
                margin-bottom: 0;
                font-size: @font-size-14;
            }
            span {
                float: right;
                color: @gray-light;
                i {
                    color: #bba99b;
                }
            }
        }
    }
}



.nav-tabs-index {
    margin-bottom: 5px;
    > li {
        margin-bottom: 6px;
        &.nav-tabs-name {
            width: 500px;
            line-height: 36px;
            &:extend(.clearfix all);

            span {
                padding: 0;
                line-height: 26px;
                display: inline-block;
                float: left;
                font-size: @font-size-16;
            }
            span.ico-common {
                width: 26px;
                height: 26px;
                margin-left: 10px;
                margin-right: 10px;
            }
        }
        &.nav-tabs-more {
            width: 80px;
            padding-top: 4px;
            padding-left: 10px;
            padding-right: 5px;
            text-align: right;
            a.ico-common {
                margin-right: 6px;
                padding: 0;
                width: 18px;
                height: 18px;
                display: inline-block;
            }
            a.ico-common:hover {
                background-color: transparent;
            }
        }
        &.default{
            a {
                padding: 2px 5px;
                margin-right: 15px;
                border-radius: 0;
                background-color: transparent;
                &:hover,
                &:focus {
                    border-bottom: 2px solid #ed4e39;
                    background-color: transparent;
                    color: #ed4e39;
                }
            }
        }
        &.active > a {
            border-radius: 0;
            border-bottom: 2px solid #ed4e39;
            background-color: transparent;
            color: #ed4e39;
        }
    }
}

.nav-tabs-report {
    margin-top: 0;
    border-color: #27a9e3;
    > li {
        &.default{
            a {
                padding: 2px 20px;
                margin-left: 0;
                margin-right: 10px;
                border-radius: 0;
                //background-color: transparent;
                &:hover,
                &:focus {
                    //border-bottom: 2px solid #ed4e39;
                    //background-color: transparent;
                    //color: #ed4e39;
                }
            }
        }
        &.active > a {
            border-radius: 0;
            //border-bottom: 2px solid #ed4e39;
            background-color: #27a9e3;
            color: @white;
        }
    }
}
.item-report {
    margin-right: 5px;
}
.item-report2 {
    margin-right: 0;
    .box-report .tab-content {
        height: 209px;
    }
    .nav-tabs-report {
        > li {
            &.default{
                a {
                    padding: 2px 10px;
                    margin-right: 8px;
                }
            }
        }
    }
}
.item-report3 {
    .box-report .tab-content {
        height: 277px;
    }
    .nav-tabs-report {
        margin-bottom: 5px;
    }
}

.item-use {
    margin-left: 5px;
}

.box-report {
    padding: 0 10px;
    .tab-content {
        height: 210px;
    }
    .box-report-list {
        li {
            padding: 8px 0 8px 10px;
            background: url(images/icon_dot.png) no-repeat left center;
            border-bottom: 1px dotted @gray-lighter;
            span {
                float: right;
                color: @gray-light;
            }
        }
    }
}

.box-use {
    padding: 0 10px;

    .table {
        margin-bottom: 8px;
        > thead > tr {
            background-color: #f2f2f2;
            #gradient > .vertical(@start-color: #f2f2f2; @end-color: #f2f2f2; @start-percent: 0%; @end-percent: 100%);
        }
        > thead > tr > th {
            border-bottom: 0;
            border-left: 3px solid @white;
            &:first-child {
                border-left: 0;
            }
        }
        > tbody > tr > td {
            border-top: 0;
            padding: 11px 0;
        }
        .choose-gys {
            margin-top: 3px;
            cursor: pointer;
        }
    }
}

.box-fun {
    height: 240px;
}

.choose-box {
    > ul {
        margin-bottom: 0;
        li {
            padding: 5px 0;
        }
    }
}

.box-work {
    height: 307px;
    &:extend(.clearfix all);
    li {
        width: 50%;
        float: left;
        display: inline-block;
        text-align: center;
        > a {
            text-decoration: none;
            > span {
                display: block;
                width: 60px;
                height: 60px;
                padding: 15px;
                margin: 12px 0 9px 32px;
                background-color: @brand-primary;
                border-radius: 3px;
                i {
                    display: block;
                    width: 30px;
                    height: 30px;
                }
                &.work-add {
                    background-color: #f8f8f8;
                    border: 2px solid #e3ecf3;
                    padding-left: 13px;
                    padding-top: 14px;
                    &:hover {
                        border-color: #cde2f2;
                    }
                }
            }
        }
    }
}

.sidebar-shopcart {
    width: 280px;
    &:extend(.clearfix all);

    .shopcart-list {
        //max-height: 356px;
        height: 100%;
        margin-left: 10px;
        overflow-y: auto;
        &:extend(.clearfix all);

        .shopcart-item {
            border-top: 1px dotted @gray-light;
            padding-top: 5px;
            margin-top: 5px;
            &:first-child {
                margin-top: 0;
                padding-top: 0;
                border-top: 0;
            }
            &:extend(.clearfix all);
            > h3 {
                margin: 0;
                padding: 8px;
                background-color: @gray-lighter;
            }
            > ul {
                padding: 5px 0 0;
                > li {
                    float: left;
                    margin-left: 6px;
                    &.goods-thumb {
                        width: 56px;
                    }
                    &.goods-name {
                        width: 128px;
                    }
                    &.goods-attr {
                        width: 56px;
                    }
                    &:first-child {
                        margin-left: 0;
                    }
                    > span {
                        display: block;
                        text-align: right;
                        &:first-child {
                            color: @red;
                        }
                    }
                    a {
                        color: @gray-dark;
                    }
                }
            }
        }
    }
    .shopcart-info {
        padding: 5px 10px;
        background-color: @gray-lighter;
        &:extend(.clearfix all);
        span {
            line-height: 30px;
        }
        .btn {
            padding-left: 10px;
            padding-right: 10px;
            background-color: @red;
            color: @white;
        }
    }
}
